<template>
    <div id="ar_container"></div>
    <!-- <button v-on:click="setArEngine()"></button> -->

</template>

<script>
import * as THREE from "three"
import { ARButton } from '../assets/ARButton.js';

export default {
    name: "ArButton",
    props:[
        "engine",
        "tracking",
        "model",
        "buttonStyle"
    ],
    data(){
        return{
            webxr_camera: null,
            webxr_scene: null,
            webxr_renderer: null,
            webxr_controller: null,
            webxr_container: null
        }
    },
    methods:{
        setArEngine(){
           
            console.log(this.$props)
        },
        initWebXR(){	

            this.webxr_scene = new THREE.Scene();

            this.webxr_camera = new THREE.PerspectiveCamera( 70, window.innerWidth / window.innerHeight, 0.01, 20 );

            var light = new THREE.HemisphereLight( 0xffffff, 0xbbbbff, 1 );
            light.position.set( 0.5, 1, 0.25 );
            this.webxr_scene.add( light );

            //

            this.webxr_renderer = new THREE.WebGLRenderer( { antialias: true, alpha: true } );
            this.webxr_renderer.setPixelRatio( window.devicePixelRatio );
            this.webxr_renderer.setSize( window.innerWidth, window.innerHeight );
            this.webxr_renderer.xr.enabled = true;

            //
            var button = ARButton.createButton( this.webxr_renderer, true ); 
            this.webxr_container.appendChild( button );

            //
            this.webxr_controller = this.webxr_renderer.xr.getController( 0 );
            this.webxr_controller.addEventListener( 'select', this.webxr_onSelect );
            this.webxr_scene.add( this.webxr_controller );

        },
        webxr_onSelect() {
            var geometry = new THREE.CylinderBufferGeometry( 0, 0.05, 0.2, 32 ).rotateX( Math.PI / 2 );
            var material = new THREE.MeshPhongMaterial( { color: 0xffffff * Math.random() } );
            var mesh = new THREE.Mesh( geometry, material );
            mesh.position.set( 0, 0, - 0.3 ).applyMatrix4( this.webxr_controller.matrixWorld );
            mesh.quaternion.setFromRotationMatrix( this.webxr_controller.matrixWorld );
            this.webxr_scene.add( mesh );
        },
        webxr_Animate(){
            this.webxr_renderer.setAnimationLoop(  this.webxr_Render );
        },
        webxr_Render(){
            this.webxr_renderer.render( this.webxr_scene, this.webxr_camera );
        },
        onWindowResize(){
            this.webxr_camera.aspect = window.innerWidth / window.innerHeight;
            this.webxr_camera.updateProjectionMatrix();

            this.webxr_renderer.setSize( window.innerWidth, window.innerHeight );
        },
    },
    created(){

    },
    mounted(){
        this.webxr_container = document.querySelector("#ar_container");
        window.addEventListener( 'resize', this.onWindowResize, false );

        if(this.engine == "WebXR"){
            this.initWebXR();
        }
    },
    beforeDestroy(){
        window.removeEventListener( 'resize', this.onWindowResize, false );
        if( this.webxr_controller != null){
            this.webxr_controller.removeEventListener( 'select', this.webxr_onSelect);
        }
       
    }
}
</script>

<style scoped>
    #ar_container{
        width: 100px;
    }
</style>